.gpt3__footer {
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--color-footer);
}

.gpt3__footer-heading{
    width: 100%;
    text-align: center;
    margin-bottom: 3rem;
}

.gpt3__footer-heading h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 60px;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.gpt3__footer-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    border: 1px solid white;
    text-align: center;
    margin-bottom: 10rem;
    cursor: pointer;
}

.gpt3__footer-btn p{
    font-family: var(--font-family);
    font-size: 18px;
    line-height: 20px;
    color: #FFF;
    word-spacing: 2px;
}

.gpt3__footer-links {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    text-align: left;
}

.gpt3__footer-links div{
    margin: 1rem;
}

.gpt3__footer-links__logo{
    display: flex;
    flex-direction: column;
}

.gpt3__footer-links__logo img{
    width: 118px;
    height:30px;
    margin-bottom: 1rem;
}

.gpt3__footer-links__logo p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: #FFF;
}

.gpt3__footer-links__div{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.gpt3__footer-links__div h4{
    font-family: var(--font-family);
    font-size: 14px;
    line-height: 16px;
    color: #FFF;
    margin-bottom: 1rem;
}

.gpt3__footer-links__div p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: #FFF;
    margin: 0.5rem 0;
    cursor: pointer;
}

.gpt3__footer-copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}

.gpt3__footer-copyright p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: #FFF;
}

@media screen and (max-width: 850px){
    .gpt3__footer-header h1{
        font-size: 42px;
        line-height: 50px;
    }

}

@media screen and (max-width: 550px){
    .gpt3__footer-header h1{
        font-size: 32px;
        line-height: 42px;
    }
    .gpt3__footer-btn p{
        font-size: 14px;
        line-height: 20px;
    }
    .gpt3__footer-links div {
        margin: 1rem 0;
    }
}

@media screen and (max-width: 550px){
    .gpt3__footer-header h1{
        font-size: 27px;
        line-height: 38px;
    }
}