.gpt3__featuresContainer__feature {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 2.5rem;
}

.gpt3__featuresContainer__feature-title{
    flex:1;
    max-width: 200px;
    margin-right: 2rem;
}

.gpt3__featuresContainer__feature-title h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 20px;
    line-height: 30px;
    letter-spacing: -0.04em;
    color: #FFF;
}

.gpt3__featuresContainer__feature-title div {
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    margin-bottom: 0.25rem;
       
}

.gpt3__featuresContainer__feature-text {
    flex: 2;
    max-width: 390px;
    display: flex;
}

.gpt3__featuresContainer__feature-text p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text);
}

@media screen and (max-width: 550px){
    .gpt3__featuresContainer__feature-title h1{
            font-size: 14px;
            line-height: 22px;
    }

    .gpt3__featuresContainer__feature-text p{
        font-size: 12px;
        line-height: 20px;
    }
    .gpt3__featuresContainer__feature{
        margin: 1rem 0;
    }
}
